@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    
  }
  
:root {
    --background-color: #0B0C0D;
    --foreground-color: #101212;
    --accent-color: #FF0000;
}

body {
    background-color: #0B0C0D;
}

p:hover {
    transition: .3s;
    color: red;
}

p {
    transition: .3s;
}


@font-face {
    font-family: 'hack';
    src: url('/public/Hack-Bold.ttf') format('truetype'),
         url('/public/Hack-BoldItalic.ttf') format('truetype'),
         url('/public/Hack-Italic.ttf') format('truetype'),
         url('/public/Hack-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  }
  